.single-gallery {
  display: flex;
  position: relative;
  height: 540px;
  margin-bottom: 40px;
}

.single-gallery a {
  display: block;
  border-radius: 4px;
  overflow: hidden;
}

.single-gallery a:nth-child(n+4) {
  display: none;
}

.single-gallery a:hover img {
  transform: scale(1.1);
}

.single-gallery a img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: .3s all ease;
}

.single-gallery a:first-child {
  width: 65.8%;
  height: 100%;
}

.single-gallery a:nth-child(2), .single-gallery a:nth-child(3) {
  position: absolute;
  right: 0;
  width: 31.7%;
  height: 255px;
}

.single-gallery a:nth-child(2) {
  top: 0;
}

.single-gallery a:nth-child(3) {
  bottom: 0;
}

.one-image_gallery a:first-child {
  width: 100%;
  height: 100%;
}

.one-image_gallery a:nth-child(n+2) {
  display: none;
}

.fleet-mobile_gallery {
  display: none;
}

@media screen and (max-width: 990px) {
  .single-gallery {
    height: 350px;
  }
  .single-gallery a:nth-child(2), .single-gallery a:nth-child(3) {
    height: 165px;
  }
}

@media screen and (max-width: 500px) {
  .single-content {
    padding-top: 0;
  }
  .fleet-mobile_gallery {
    display: block;
    position: relative;
  }
  .single-gallery {
    display: none;
  }
}

